<template>
    <div>
        <ul>
            <li v-for="(item,index) in pageLists" :key="index">
                <div class="main">
                    <p>标题：</p>
                    <p>{{item.title}}</p>
                    <p>内容：</p>
                    <p>{{item.content}}</p>
                </div>
            </li>
        </ul>
    </div>

</template>

<script>
    import store from '@/store'

    export default {
        name: "List",
        store,
        computed: {
            pageLists() {
                return store.state.lists
            }
        }
    }
</script>

